<template>
<div class="theme-color">
    <div class="text-4xl text-center p-4">Pick a lesson to learn</div>
    <div class="flex flex-row flex-wrap">
        <van-card v-for="(lesson, index) in lessons" :key="index" :thumb="lesson.cover" class="w-[calc(33%-20px)] m-2 shadow-md rounded-xl">
            <template #title>
                <div class="text-xl">{{ lesson.name }}</div>
            </template>
            <template #desc>
                <div>{{ lesson.info }}</div>
            </template>
            <template #num>
                <div class="py-2">{{ lesson.author }}</div>
            </template>
            <template #footer>
                <van-button size="small" type="primary" @click="goLearn(lesson)">Learn</van-button>
            </template>
        </van-card>
    </div>
</div>
</template>

<script>
import {
    ref
} from "vue";
import {
    useRouter
} from "vue-router";
import {
    useStore
} from "vuex";
import {
    Button,
    Card
} from "vant";
import lesson1 from "@/values/lesson.json";

export default {
    name: "ListView",
    components: {
        [Button.name]: Button,
        [Card.name]: Card
    },
    setup() {
        const store = useStore();
        const router = useRouter();
        const lessons = ref([{
            name: "Narrative Writing 01",
            info: "This is our first demo lesson to guide you through a fun creative writing process!",
            cover: "images/test4.png",
            file: "lesson.json",
            author: "Teacher Miss A."
        }]);

        const goLearn = function (lesson) {
            if (lesson.file === "lesson.json") {
                store.commit("setLesson", lesson1);
                router.push("/study");
            }
        };

        return {
            lessons,
            goLearn
        };
    }
}
</script>

<style scoped>
.theme-color {
    background-color: #e0e9f5;
    height: 100vh;
}
</style>
